import './Login.css';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import application from '../../firebase';
import { getAuth, signInWithEmailAndPassword } from 'firebase/auth';

function Login() {
    
    //User Authentication
    const auth = getAuth(application);
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');

    async function signIn() {
        await signInWithEmailAndPassword(auth, email, password)
        .then((userCredential) => {
            const user = userCredential.user;
            console.log(user);
        })
        .catch((error) => {
            const errorMessage = error.message;
            alert(errorMessage);
        });
    }

    return (
        <>

            <Navigation />

            <main>
                <section className='middlebar'>

                    <p className='warning'>It's awesome to get you back!<br/>Login to start exploring again!</p>

                    <div className='component-login'>

                        <nav className='leftlogin'>    
                            <form action={'/store.parrotcloud/store?'}>
                                <label>E-mail</label>
                                <input type={'email'} required placeholder='Email' onChange={(e) => setEmail(e.target.value)}/>

                                <label>Password</label>
                                <input type={'password'} required placeholder='Password' onChange={(e) => setPassword(e.target.value)}/>

                                <p>Consider doing the the 2 step verification, to improve your security!</p>

                                <button onClick={signIn}>Login</button>
                                <br/>
                                <Link to='/sign-up' className='terciary-button'>Create Account</Link>
                            </form>
                        </nav>

                        <nav className='rigthlogin'>
                            <p>You can enter up using</p>
                        </nav>
                    
                    </div>

                </section>
            </main>
        </>
    );
}

const Navigation = () => {
    return (
        <header>
            <nav className='upheader'>
                <span>
                    <Link to='/store.parrotcloud/' className='logo'>ParrotCloud_</Link>  
                </span>
                <span>
                    <Link to='/store.parrotcloud/sign-up' className='login'>Sign Up</Link>
                </span>
            </nav>
        </header>
    );
}

export default Login;