import './GameCard.css';
import GamePromo from './GamePromo/GamePromo';

const GameCard = (props) => {

    let game = [];
    for (let i = 0; i < 8; i++) {
        game.push(
            <GamePromo
            key={props.id}
            />    
        )
    }

    return (
        <div className='gamecard'>
            {game}
        </div>
    )
}

export default GameCard;